/*style.scss*/
@mixin placeholder {
    &::-moz-placeholder {
        @content
    }
    &::-webkit-input-placeholder {
        @content
    }
    &:-ms-input-placeholder {
        @content
    }
}

@mixin Lines($count,$lh){
	line-height:$lh;
	max-height: $count*$lh;
	overflow: hidden;
}

