/*-------------------------------  Defs & Sys  ------------------------------------*/

body {
    min-width: 320px;
    font-size: 14px;
    line-height: 1.4;
    color: #555555;
    background-color: #fff;
    -webkit-font-smoothing: antialiased;
    font-family: Arial, sans-serif;
    font-weight: 400;
}

h1,
.h1 {
    font-family: 'PlainScriptCTT';
    text-transform: uppercase;
    font-size: 32px;
    color: #cf4534;
    margin: 0 0 20px 0;
}

h1 {
    text-align: center;
    overflow: hidden;
    span {
        display: inline-block;
        vertical-align: baseline;
        zoom: 1;
        position: relative;
        padding: 0 20px;
        &:before,
        &:after {
            content: '';
            display: block;
            width: 500px;
            position: absolute;
            height: 100%;
            top: 0;
        }
        &:before {
            right: 100%;
            background-image: url("../images/header-before.png");
            background-repeat: no-repeat;
            background-position: 100% 50%;
        }
        &:after {
            left: 100%;
            background-image: url("../images/header-after.png");
            background-repeat: no-repeat;
            background-position: 0% 50%;
        }
    }
}

.h1--center {
    position: relative;
    overflow: hidden;
    text-align: center;
    line-height: 1;
    margin-bottom: 0;
    span {
        display: inline-block;
        vertical-align: baseline;
        zoom: 1;
        position: relative;
        padding: 0 20px;
        &:before,
        &:after {
            content: '';
            display: block;
            width: 500px;
            position: absolute;
            height: 100%;
            top: 0;
        }
        &:before {
            right: 100%;
            background-image: url("../images/header-before.png");
            background-repeat: no-repeat;
            background-position: 100% 50%;
        }
        &:after {
            left: 100%;
            background-image: url("../images/header-after.png");
            background-repeat: no-repeat;
            background-position: 0% 50%;
        }
    }
}

.tac{
	text-align: center;
}

a {
    color: #43f;
}

.clear {
    clear: both;
}

.clearfix:after,
.page-zize:after {
    content: '';
    display: block;
    clear: both;
    height: 0;
}

.svgHolder > svg {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    max-width: 100%;
    max-height: 100%;
    margin: auto;
}


/*-------------------------------  hamburger-button  ------------------------------------*/

.hamburger-button {
    cursor: pointer;
    display: none;
    position: relative;
    width: 30px;
    height: 20px;
    float: right;
    margin: 75px 0 0;
    span {
        display: block;
        height: 2px;
        width: 100%;
        position: absolute;
        left: 0;
        top: 50%;
        background: #CF4534;
        &:before {
            content: '';
            display: block;
            position: absolute;
            background: #CF4534;
            height: 2px;
            width: 100%;
            top: -6px;
            -webkit-transition: all 0.2s ease;
            transition: all 0.2s ease;
        }
        &:after {
            content: '';
            display: block;
            position: absolute;
            background: #CF4534;
            height: 2px;
            width: 100%;
            bottom: -6px;
            -webkit-transition: all 0.2s ease;
            transition: all 0.2s ease;
        }
    }
    &:hover span {
        &:before {
            top: -4px;
        }
        &:after {
            bottom: -4px;
        }
    }
}


