/*-------------------------------  metro-list  ------------------------------------*/

.metro-list {
    margin: 0px 0 15px 0;
}

.metro-list_item {
    display: inline-block;
    margin: 0 10px 0 0;
    padding: 0 0 0 20px;
    color: #64c1c4;
    font-size: 12px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: 0 15%;
}


/*-------------------------------  breadcrumbs  ------------------------------------*/

.breadcrumbs {
    margin: 0 0 20px 0;
    font-size: 12px;
    a {
        color: #555555;
        display: inline-block;
        margin: 0 0px 0 0;
        text-decoration: underline;
        position: relative;
        &:after {
            content: '';
            margin: 0 5px 0 8px;
            display: inline-block;
        }
        &:hover {
            text-decoration: none;
        }
    }
    span {
        color: #cf4534;
    }
}


/*-------------------------------  button  ------------------------------------*/

.button {
    height: 44px;
    line-height: 44px;
    text-align: center;
    display: inline-block;
    font-size: 13px;
    padding: 0 50px;
    border-radius: 2px;
    text-decoration: none;
    border: none;
    -webkit-transition: background 0.2s ease-in;
    transition: background 0.2s ease-in;
    box-sizing: border-box;
	&--right{
		float: right;
		margin-bottom: 15px;
	}
}

.button_plus {
    display: inline-block;
    position: relative;
    vertical-align: middle;
    width: 16px;
    height: 16px;
    margin: 0 10px 0 0;
    top: -2px;
    -webkit-transition: -webkit-transform 0.4s ease-in;
    transition: -webkit-transform 0.4s ease-in;
    transition: transform 0.4s ease-in;
    transition: transform 0.4s ease-in, -webkit-transform 0.4s ease-in;
}

.button:hover .button_plus {
    -webkit-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
}

.button_plus {
    &:before {
        content: '';
        position: absolute;
        width: 2px;
        height: 16px;
        left: 50%;
        top: 0;
        margin: 0 0 0 -1px;
        background: #fff;
    }
    &:after {
        content: '';
        position: absolute;
        width: 16px;
        height: 2px;
        top: 50%;
        left: 0;
        margin: -1px 0 0;
        background: #fff;
    }
}

.button--red {
    color: #fff;
    background: #cf4534;
    &:hover {
        background: #a83628;
    }
}


.button--green {
	color: #fff;
	background: #62ad5e;
	&:hover {
		background: #68b264;
	}
}

/*-------------------------------  seo-text  ------------------------------------*/

.seo-text {
    font-size: 14px;
    color: #555555;
    h1,
    p {
        margin: 0 0 20px 0;
    }
}


/*-------------------------------  search  ------------------------------------*/

.search-form {
    position: relative;
    border: 2px solid #e9e9e9;
    background: #f4f4f4;
    padding: 15px;
    border-radius: 4px;
    margin: 30px 0 15px 0;
}

.search-form_location {
    position: relative;
    cursor: pointer;
    float: left;
    height: 42px;
    border: 1px solid #d5d4d4;
    width: 280px;
    box-sizing: border-box;
    line-height: 40px;
    background: #fff;
    border-radius: 2px;
    padding: 0 0 0 34px;
    font-size: 13px;
    color: #d3d3d3;
    font-style: italic;
    margin: 0 10px 0 0;
    background-image: url("../images/location.svg");
    background-repeat: no-repeat;
    background-position: 10px 50%;
    &:after {
        content: '';
        position: absolute;
        right: 10px;
        top: 42%;
        opacity: 0.5;
        border-top: 6px solid #000;
        border-left: 4px solid transparent;
        border-right: 4px solid transparent;
        -webkit-transition: all 0.2s ease;
        transition: all 0.2s ease;
    }
    &:hover:after {
        opacity: 1;
    }
}

.search-form_field {
    overflow: hidden;
    input {
        width: 100%;
        box-sizing: border-box;
        height: 42px;
        border: 1px solid #d5d4d4;
        border-radius: 2px;
        background: #fff;
        padding: 0 15px;
        font-style: italic;
        font-size: 13px;
        color: #b3b3b3;
    }
    @include placeholder {
        color: #d3d3d3;
    }
}

.search-form_button.wFormRow {
    float: right;
    width: 160px;
    box-sizing: border-box;
    margin-left: 10px;
    button {
        display: block;
        border: none;
        height: 42px;
        width: 100%;
        background-color: #60bfc3;
        padding: 0 0 0 60px;
        margin: 0;
        text-align: left;
        color: #fff;
        font-size: 13px;
        border-radius: 2px;
        -webkit-transition: all 0.2s ease-in;
        transition: all 0.2s ease-in;
        background-image: url("../images/find.svg");
        background-repeat: no-repeat;
        background-position: 25px 50%;
        &:hover {
            background-color: #42aaae;
        }
    }
}

.location-list {
    display: block;
    position: absolute;
    visibility: hidden;
    opacity: 0;
    z-index: 100;
    background: #fff;
    border-radius: 2px;
    left: 15px;
    right: 15px;
    top: 70%;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.5);
    -webkit-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
}

.location-list--visible {
    top: 90%;
    opacity: 1;
    visibility: visible;
}

.location-list_title {
    padding: 10px 15px;
    border-bottom: 1px solid #d5d4d4;
}

.location-list_wrap {
    -webkit-column-count: 4;
    -moz-column-count: 4;
    column-count: 4;
    -webkit-column-rule: 1px solid #d5d4d4;
    -moz-column-rule: 1px solid #d5d4d4;
    column-rule: 1px solid #d5d4d4;
    -webkit-column-gap: 0px;
    -moz-column-gap: 0px;
    column-gap: 0px;
}

.location-list_item {
    cursor: pointer;
    padding: 10px 15px;
    font-size: 14px;
    -webkit-column-break-inside: avoid;
    page-break-inside: avoid;
    break-inside: avoid;
    -webkit-transition: background 0.2s ease-in, color 0.2s ease-in;
    transition: background 0.2s ease-in, color 0.2s ease-in;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    &:hover {
        background: #60bfc3;
        color: #fff;
    }
}


/*-------------------------------  rating  ------------------------------------*/

.rating {
    width: 101px;
    height: 10px;
    position: relative;
    background-image: url("../images/rating-disable.png");
    background-repeat: no-repeat;
    overflow: hidden;
    span {
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background-image: url("../images/rating-enable.png");
        background-repeat: no-repeat;
    }
    &[data-rating="1"] span {
        left: -80px;
    }
    &[data-rating="2"] span {
        left: -60px;
    }
    &[data-rating="3"] span {
        left: -40px;
    }
    &[data-rating="4"] span {
        left: -20px;
    }
    &[data-rating="5"] span {
        left: 0;
    }
}


/*-------------------------------  star  ------------------------------------*/

.star {
    display: block;
    width: 27px;
    height: 27px;
    background-image: url("../images/star.png");
    background-repeat: no-repeat;
    background-position: 0 0;
}

.no-touch .star:hover,
.star--active {
    background-position: -27px 0;
}


