    }
}

.catalog-list {
    margin: 0 -6px;
    position: relative;
}

.grid-type{
    .catalog-item_category{
        display: none;
    }
    .catalog-item_published{
        display: none;
    }
    .catalog-list_item {
        float: left;
        padding: 0 6px;
        width: 25%;
        margin: 0 0 25px 0;
