    .catalog-item_type {
        font-size: 11px;
        color: #777777;
        padding: 0 0 0 16px;
        position: relative;
        margin: 0 0 0px 0;
        &:before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 10px;
            height: 11px;
            background-image: url(data:image/png;base64,);
            background-repeat: no-repeat;
