/*-------------------------------  wHeader  ------------------------------------*/



.page-header {
    padding: 35px 0 0 0;
    margin: 0 0 20px 0;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2);
}

.page-header_logo {
    float: left;
    margin: -12px 0 0 0;
}

.header-user-nav {
    position: absolute;
    right: 70px;
    top: -17px;
}

.page-header_star {
    position: relative;
    display: block;
    float: left;
    width: 27px;
    height: 27px;
    margin: 0 30px 0 0;
    svg {
        fill: #555555;
        -webkit-transition: all 0.2s ease;
        transition: all 0.2s ease;
    }
    &:hover {
        svg {
            fill: #cf4534;
        }
        &:before {
            color: #cf4534;
        }
    }
    &:before {
        content: attr(data-count);
        position: absolute;
        left: 32px;
        font-size: 16px;
        line-height: 27px;
        top: 4px;
        color: #555555;
        -webkit-transition: all 0.2s ease;
        transition: all 0.2s ease;
    }
}

.main-nav {
    float: right;
    margin: 26px 12px 0px 0;
}

.main-nav_item {
    float: left;
    position: relative;
    margin-left: 3px;
    &:first-child {
        .main-nav_link-icon:before {
            border: 1px solid #cc402f;
        }
        > a {
            background: #f4f4f4;
        }
