}

.main-nav_link-icon {
    display: inline-block;
    text-align: center;
    margin: 0 0 15px 0;
    position: relative;
    img {
        display: block;
    }
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        border: 1px solid #e0e0e0;
        box-sizing: border-box;
        opacity: 0;
        -webkit-transition: all 0.2s ease-in;
        transition: all 0.2s ease-in;
    }
}

.main-nav_link-text {
    display: block;
    text-align: center;
    color: #555555;
    text-decoration: none;
    position: relative;
}

.main-nav_item--has-children .main-nav_link-text:after {
    content: '';
    position: relative;
    border-top: 5px solid #000;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    margin: 0 0 0 10px;
    top: 10px;
}

.main-nav_link:hover {
    .main-nav_link-icon:before {
        opacity: 1;
        -webkit-transform: scale(1.15);
        -ms-transform: scale(1.15);
        transform: scale(1.15);
    }
    .main-nav_link-text {
        color: #cc402f;
    }
}

.main-subnav {
    position: absolute;
    z-index: 100;
    top: 80%;
    right: 0;
    width: 140px;
    padding: 5px 12px;
    background: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    visibility: hidden;
    opacity: 0;
    -webkit-transition: all 0.25s ease-in;
    transition: all 0.25s ease-in;
}

.main-nav_item:hover .main-subnav {
    visibility: visible;
    opacity: 1;
    top: 90%;
}

.main-subnav {
    &:before {
        content: '';
        position: absolute;
        right: 20px;
        top: -8px;
        border-bottom: 6px solid rgba(0, 0, 0, 0.04);
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
    }
    &:after {
        content: '';
        position: absolute;
        right: 20px;
        top: -6px;
        border-bottom: 6px solid #fff;
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
    }
}

.main-subnav_item:not(:last-child) {
