            left: -20px;
        }
        &:after {
            content: 'on';
            position: absolute;
            font-size: 12px;
            color: #999999;
            right: -20px;
        }
        span {
            display: block;
            position: absolute;
            width: 20px;
            height: 20px;
            border-radius: 10px;
            background: #b44738;
            left: 0;
            top: -1px;
            -webkit-transition: all 0.25s ease;
            transition: all 0.25s ease;
        }
