}

.enterReg_top {
    position: relative;
    margin-bottom: 20px;
    overflow: hidden;
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        width: 1px;
        height: 100%;
        background: #ddd;
    }
}

.enterBlock {
    z-index: 9;
    float: left;
    position: relative;
    width: 46%;
