            border-color:#1b88cd;
            &:before{
                content: "";
                border-top:1px solid #1b88cd;
                border-left:1px solid #1b88cd;
                border-right:1px solid #1b88cd;
                position: absolute;
                top: -5px;
                left: -1px;
                width: 100%;
                height: 5px;
                border-top-left-radius: 3px;
                border-top-right-radius: 3px;
            }
            &:after{
                content: "";
                position: absolute;
                left: -1px;
                top: 0;
                height: 100%;
                width: 1px;
                background: #1b88cd;
            }
        }
