        &:first-child{
            width: 50px;
        }
        &:nth-child(2){
            width: 195px;
        }
        &:nth-child(3){
            .inside{
                border: none;
            }
        }
        &:nth-child(4){
            width: 100px;
        }
        &:last-child{
            width: 45px;
            .inside{
                border:none;
            }
        }
        &.name{
          font-size: 14px;  
        }
    }
    .inside{
        margin: 10px 0;
