        height: 20px;
        cursor: pointer;
        &:hover,&.active{
            background-image: url(data:image/png;base64,);
        }
    }
    .remove-mes{
        position: relative;
        display: block;
        width: 15px;
        height: 15px;
        margin-left: 1px;
        &:before{
            content: "";
            width: 15px;
            height: 3px;
            background: #e6e6e6;
            position: relative;
            top: 7px;
            left: 0;
            display: block;
            transform: rotate(45deg);
        }
        &:after{
            content: "";
            width: 15px;
            height: 3px;
            background: #e6e6e6;
            position: relative;
            top: 4px;
            left: 0;
            display: block;
            transform: rotate(-45deg);
        }
        &:hover{
            &:before,&:after{
                background: #cc402f;
            }
        }
    }
